import { Route, Routes, useLocation } from 'react-router-dom';
import { Home  } from './module/public/home';
import { SignIn  } from './module/public/signIn';
import {MLayout} from './module/layout'


export const Public = () => {
  const { pathname } = useLocation();
  return (
    <>
      {pathname !== '/signin' && pathname !== '/signup'}
      <Routes>
        <Route path='*' element={<h2 className='text text-warning'>Error: this page doesn't exist</h2>}/>
        <Route path="/" element={<Home />} />
        <Route path="/signin" element={<SignIn />} />
        <Route path="/layout" element={<MLayout />} />
      </Routes>
    </>
  );
};
