import { useRef, useState, } from "react"
import { useNavigate  } from "react-router-dom"

export const SignIn = () => {
    const emailRef = useRef()
    const pswRef = useRef()
    const navigate = useNavigate()
    const [err , setErr] = useState(false)
    // const {pathname} = useLocation()

    const Login = async (event) => {
        event.preventDefault()
        const data = {
            email: emailRef.current.value,
            password: pswRef.current.value
        }
        const fetchlog = await fetch('https://reqres.in/api/login', {
            method: 'Post',
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify(data)
        })

    if(fetchlog.ok){
        navigate('/')
        localStorage.setItem('token' , JSON.stringify(data))
    }else {
        setErr(true)
    }
    
}
    return (
        <form className="w-25 mx-auto my-5 text-center d-blox" onSubmit={Login}>
            <h1>Sign in</h1>
            <div style={{display: 'flex' , flexWrap: 'wrap'}} className="alert alert-info">
                <b>Email:<mark>eve.holt@reqres.in</mark></b>
                <b>Password:<mark>cityslicka</mark></b>
            </div>
            <div>
            <input ref={emailRef} className={`form-control ${err ? 'is-invalid' : ''}`} type="email" placeholder="email..." required />
            <input ref={pswRef} className={`form-control  my-4 ${err ? 'is-invalid' : ''}`} type="password" placeholder="password..." required />
            </div>

            <button type="submit" className="btn btn-primary px-4">Login</button>
        </form>
    )
}